html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

.icon-fill-2 {
  width: 1em;
  height: 1em;
  fill: #FFFFFE;
}

.icon-inst {
  width: 1em;
  height: 1em;
  fill: #FFF;
}

@font-face {
  font-family: Geometria;
  src: url("../fonts/Geometria.woff2") format("woff2"), url("../fonts/Geometria.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Geometria-ExtraBold;
  src: url("../fonts/Geometria-ExtraBold.woff2") format("woff2"), url("../fonts/Geometria-ExtraBold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: "Geometria";
  box-sizing: border-box;
}

.center {
  margin: 0 auto;
  background-image: url("../img/bitmap.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
  z-index: 0;
}

.center:after {
  content: " ";
  display: table;
  clear: both;
}

.center:after {
  background-color: rgba(0, 0, 0, 0.4);
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.icon-fill-2 {
  width: 23px;
  height: 23px;
}

.icon-inst {
  width: 17px;
  height: 17px;
  display: block;
  margin: 0 auto;
  position: relative;
  bottom: 60px;
  cursor: pointer;
}

.icon-facebook {
  width: 17px;
  height: 17px;
  display: block;
  margin: 0 auto;
  position: relative;
  bottom: 15px;
  cursor: pointer;
}

.header {
  position: relative;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.menu {
  width: 80px;
  height: 81px;
  background-color: #4ea8d5;
  float: left;
  padding: 25px;
  text-align: center;
  cursor: pointer;
}

.menu span {
  display: block;
  width: 31px;
  height: 2px;
  background-color: #fff;
  margin: 4px auto;
}

.menu__text {
  font-size: 9px;
  font-weight: bold;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.7px;
}

.logo {
  width: 161px;
  height: 81px;
  background-color: #353643;
  padding: 20px 34px 24px 37px;
  float: left;
}

.phone {
  width: 80px;
  height: 81px;
  background-color: #4ea8d5;
  float: right;
  padding: 31px 29px 27px 28px;
  cursor: pointer;
}

.contacts {
  width: 80%;
  padding: 29px 0;
  float: right;
}

.contacts:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1606px) {
  .contacts {
    width: 75%;
  }
}

@media only screen and (max-width: 1285px) {
  .contacts {
    width: 70%;
  }
}

.contacts__part {
  width: 50%;
  float: right;
  padding: 0 25px 0 0;
  text-align: center;
}

.contacts__btn {
  margin: 0 auto;
  color: #fff;
  font-size: 12px;
  padding: 0 0 5px 0;
  background-image: none;
  background-color: transparent;
  font-weight: bold;
  font-family: "Geometria";
  border-bottom: 2px solid #4ea8d5;
}

.contacts__info {
  font-family: "Geometria";
  font-size: 12px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  float: left;
  width: 50%;
}

.contacts__info_big {
  font-size: 15px;
}

.caption {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.caption__text {
  font-family: Geometria-ExtraBold;
  font-size: 70px;
  letter-spacing: 0.5px;
  color: #fff;
  font-weight: 800;
  line-height: normal;
  text-transform: uppercase;
  margin: 0 auto;
  width: 70%;
  text-align: left;
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

.nav-list {
  float: left;
  width: 85%;
  position: absolute;
  bottom: 0;
}

.nav-list__item {
  width: 20%;
  text-align: center;
  float: left;
  padding: 30px 0 34px 0;
  cursor: pointer;
}

.nav-list__item:hover {
  border-bottom: 4px solid #43a8d5;
  padding: 30px 0;
}

.nav-list__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 0.5px;
  font-size: 12px;
  font-weight: bold;
  width: 100%;
}

.social {
  float: right;
  width: 80px;
  position: absolute;
  bottom: 0;
  right: 10px;
}

.partners {
  float: right;
  position: absolute;
  bottom: 30px;
  right: 80px;
}

.partners__img {
  cursor: pointer;
}

.more__btn {
  background-color: #353643;
  color: #fff;
  text-transform: uppercase;
  font-size: 8px;
  font-weight: bold;
  padding: 5px 6px;
  letter-spacing: 0.3px;
  font-family: "Geometria";
  position: relative;
}

.more__btn:before {
  content: "+";
  width: 16px;
  height: 20px;
  position: absolute;
  left: -20px;
  top: 0;
  background-color: inherit;
  font-size: 15px;
}

.more {
  position: absolute;
  top: -100px;
  right: 20%;
}
